import { FormControl } from '@angular/forms';
import { BehaviorSubject } from 'rxjs';
import { distinctUntilChanged } from 'rxjs/operators';

export class RecordEditorFormControl extends FormControl {
  private _touchedSubject = new BehaviorSubject(this.touched);

  readonly touched$ = this._touchedSubject.asObservable().pipe(distinctUntilChanged());

  markAsTouched(opts?: { onlySelf?: boolean }): void {
    super.markAsTouched(opts);

    this._touchedSubject.next(this.touched);
  }

  markAsUntouched(opts?: { onlySelf?: boolean }): void {
    super.markAsUntouched(opts);

    this._touchedSubject.next(this.touched);
  }
}
